@media screen and (max-width: 1000px) {
    .container-project {
        padding: 5vh !important;
        margin-top: 15vh;
    }

    .first-section:first-child {
        margin-left: 5vh !important;
    }
    .title-name {
        opacity: 0.3;
    }
    .title-homecard {
        font-size: 1rem !important;
        width: -webkit-fill-available !important;
    }

    .title-project {
        font-size: 2rem;
    }

    .project img {
        width: 85vw;
    }

    .skills {
        margin-left: 5vh;
    }

    .email {
        font-size: 1rem !important;
    }
}
